#root {
  max-width: 1000px;
}

.hidden{
  visibility: hidden;
}

body {
  min-height: 100vh;
  margin: 0;
  display: flex;
  justify-content: center;
}

#d-f{
  display: flex;
}

#danger{
  color: red;
}

#j-a{
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px;
}

/* /////////////////// END OF THE GLOBAL /////////////////// */

.cart {
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.cart div {
  width: 30px;
  height: 30px;
  border-radius: 50%;
}


/* //////////////////////////// HEADER IMAGES SLIDER///////////// */
#active{
  display: block;
}