.headerImagesSlider img {
  display: none;
  width: 100%;
  height: 70vh;
  object-fit: cover;
}

.box{
  display: flex;
  flex-wrap: wrap;
}

.box img {
  width: 25%;
  height: 200px;
  object-fit: cover;
  cursor: pointer;
  /* border:  1px solid black; */
  padding: 1px;
  box-sizing: border-box;
 }

 .see {
   display: flex;
   justify-content: space-around;
   background-color: black;
   color: white;
   cursor: pointer;
 }

 .category {
   display: flex;
   justify-content: space-around;
   cursor: pointer;
 }

 @media screen and (max-width: 500px) {
   .box img {
     width: 50%;
   }

   .headerImagesSlider img{
     height: 30vh;
   }
 }

 @media screen and (min-width:500px) and (max-width: 800px) {
   .box img {
     width: 33.3333333333333%;
   }
   .headerImagesSlider img{
    height: 45vh;
  }

 }